import React, { Suspense } from "react";
import { BrowserRouter, Router, Switch } from "react-router-dom";
import history from "./History";
import * as LazyComponent from "../utils/LazyLoaded";
import Loader from "../components/Loader/Loader";

const Routes = ({}) => {
  return (
    <Suspense fallback={<Loader />}>
      <BrowserRouter basename={process.env.REACT_APP_BASEURL} history={history}>
        <Switch>
          <LazyComponent.Login path="/login/:token" exact />
          <LazyComponent.Instructions path="/Instructions" exact />
          <LazyComponent.Preview path="/preview" exact />
          <LazyComponent.Parent path="/interview-process" exact />
          <LazyComponent.Interview path="/interview" exact />
          <LazyComponent.Finish path="/congratulation" exact />
          <LazyComponent.Submitting path="/submitting" exact />
          <LazyComponent.InterviewMcq path="/interview-mcq" exact />
          <LazyComponent.Error path="/error" exact />
          <LazyComponent.Test path="/temporary" exact />
          <LazyComponent.Maintainance path="/maintainance" exact />
          <LazyComponent.Editor path="/editor" exact />
          <LazyComponent.NotFound path="/*" />
        </Switch>
      </BrowserRouter>
    </Suspense>
  );
};

export default Routes;
