import React from "react";
import Navbar from "../components/Navbar/Navbar";
import { Router } from "react-router-dom";
import history from "../routes/History";
import Routes from "../routes/Routes";
import SideNav from "../sideNav/sideNav";
import Loader from "../components/Loader/Loader";
import "./App.scss";
import "react-notifications-component/dist/theme.css";
import { store } from "react-notifications-component";
import birballogo from "../assets/images/instructions/birballogo.png";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import ReactGA from "react-ga";
import { bindActionCreators } from "redux";
import * as Actions from "../store/User/UserAction";

ReactGA.initialize("UA-58884491-1");

class App extends React.Component {
  state = {
    navClass: true,
    responseId: null,
  };

  async componentDidMount() {
    const { responseId } = this.state;
    window.onunload = function (e) {
      // Firefox || IE
      e = e || window.event;

      var y = e.pageY || e.clientY;

      if (y < 0) {
        responseId &&
          this.props.UserAction.addEvent({
            responseId: responseId,
            event: {
              type: "browser",
              level: 2,
              metadata: "Window close triggered",
              timestamp: new Date().toISOString(),
              route: window.location.pathname,
            },
          });
      } else {
        responseId &&
          this.props.UserAction.addEvent({
            responseId: responseId,
            event: {
              type: "browser",
              level: 2,
              metadata: "Refresh triggered",
              timestamp: new Date().toISOString(),
              route: window.location.pathname,
            },
          });
      }
    };
    document.addEventListener("contextmenu", (e) => {
      e.preventDefault();
      // responseId &&
      //   this.props.UserAction.addEvent({
      //     responseId: responseId,
      //     event: {
      //       type: "browser",
      //       level: 2,
      //       metadata: "Right click triggered",
      //       timestamp: new Date().toISOString(),
      //     },
      //   });
    });
    // document.addEventListener("visibilitychange", (event) => {
    //   if (document.visibilityState == "visible") {
    //     responseId &&
    //       this.props.UserAction.addEvent({
    //         responseId: responseId,
    //         event: {
    //           type: "browser",
    //           level: 2,
    //           metadata: "tab is active",
    //           timestamp: new Date().toISOString(),
    //         },
    //       });
    //   } else {
    //     responseId &&
    //       this.props.UserAction.addEvent({
    //         responseId: responseId,
    //         event: {
    //           type: "browser",
    //           level: 2,
    //           metadata: "tab is inactive",
    //           timestamp: new Date().toISOString(),
    //         },
    //       });
    //   }
    // });
  }

  async componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.UserReducer;
    const cur = this.props && this.props.UserReducer;
    if (
      prev.userDataResponse !== cur.userDataResponse &&
      cur.userDataResponse &&
      cur.isUserDataAdded
    ) {
      const { response } = cur.userDataResponse;
      let newResponseId = response && response.id;
      const responseId = await localStorage.getItem("responseId");
      if (newResponseId == responseId) {
        this.setState({ responseId: responseId });
        document.addEventListener("contextmenu", (e) => {
          e.preventDefault();
          //   responseId &&
          //     this.props.UserAction.addEvent({
          //       responseId: responseId,
          //       event: {
          //         type: "browser",
          //         level: 2,
          //         metadata: "Right click triggered",
          //         timestamp: new Date().toISOString(),
          //       },
          //     });
        });
        // document.addEventListener("visibilitychange", (event) => {
        //   if (document.visibilityState == "visible") {
        //     responseId &&
        //       this.props.UserAction.addEvent({
        //         responseId: responseId,
        //         event: {
        //           type: "browser",
        //           level: 2,
        //           metadata: "tab is active",
        //           timestamp: new Date().toISOString(),
        //         },
        //       });
        //   } else {
        //     responseId &&
        //       this.props.UserAction.addEvent({
        //         responseId: responseId,
        //         event: {
        //           type: "browser",
        //           level: 2,
        //           metadata: "tab is inactive",
        //           timestamp: new Date().toISOString(),
        //         },
        //       });
        //   }
        // });
        window.onunload = function (e) {
          // Firefox || IE
          e = e || window.event;

          var y = e.pageY || e.clientY;

          if (y < 0) {
            responseId &&
              this.props.UserAction.addEvent({
                responseId: responseId,
                event: {
                  type: "browser",
                  level: 2,
                  metadata: "Window close triggered",
                  timestamp: new Date().toISOString(),
                  route: window.location.pathname,
                },
              });
          } else {
            responseId &&
              this.props.UserAction.addEvent({
                responseId: responseId,
                event: {
                  type: "browser",
                  level: 2,
                  metadata: "Refresh triggered",
                  timestamp: new Date().toISOString(),
                  route: window.location.pathname,
                },
              });
          }
        };
      }
    }
  }

  toggleClass = () => {
    this.setState({ navClass: !this.state.navClass }, () => {});
  };

  selectionOperation = (type) => {
    const { responseId } = this.state;
    responseId &&
      this.props.UserAction.addEvent({
        responseId: responseId,
        event: {
          type: "browser",
          level: 2,
          metadata: `${type} action triggered`,
          timestamp: new Date().toISOString(),
        },
      });
    return false;
  };
  loadHandler = () => {
    const { responseId } = this.state;
    responseId &&
      this.props.UserAction.addEvent({
        responseId: responseId,
        event: {
          type: "browser",
          level: 2,
          metadata: "Refresh triggered",
          timestamp: new Date().toISOString(),
        },
      });
  };
  render() {
    const { pathname } = history.location;

    return (
      // <IntlProvider locale={lang} messages={messages[lang]}>
      <div
        className='wrapper'
        onCopy={() => this.selectionOperation("Copy")}
        onCut={() => this.selectionOperation("Cut")}
        onPaste={() => this.selectionOperation("Paste")}
        // onLoad={() => this.loadHandler()}
      >
        <Router history={history}>
          <div className='mainPage'>
            <Routes />
          </div>
        </Router>
      </div>
      // </IntlProvider>
    );
  }
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});

function mapDispatchToProps(dispatch) {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(App));
