import * as types from "./CodeEdiorTypes";

const INITIAL_STATE = {
  isSubmitCode: false,
  submitCode: [],
  isGetCode: false,
  getCode: [],
};

export function CodeEditorReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case types.SUBMIT_CODE:
      return {
        ...state,
        isSubmitCode: false,
        submitCode: [],
      };

    case types.SUBMIT_CODE_SUCCESS:
      return {
        ...state,
        isSubmitCode: true,
        submitCode: action.payload,
      };

    case types.SUBMIT_CODE_ERROR:
      return {
        ...state,
        isSubmitCode: false,
        submitCode: action.payload,
      };

    case types.GET_CODE:
      return {
        ...state,
        isGetCode: false,
        getCode: [],
      };

    case types.GET_CODE_SUCCESS:
      return {
        ...state,
        isGetCode: true,
        getCode: action.payload,
      };

    case types.GET_CODE_ERROR:
      return {
        ...state,
        isGetCode: false,
        getCode: action.payload,
      };

    default:
      return state;
  }
}
