import axios from "./axios";
import local from "./localStorage";

class ApiJunction {
  makeRequest(params) {
    let token = params.token || local.getItem("tokenNew");
    axios.defaults.headers.common["Authorization"] = `${token}`;
    axios.defaults.headers.common["Content-Type"] = `application/json`;
    // if (params.headers) {
    //   Object.entries(params.headers).map(([key, val]) => {
    //     axios.defaults.headers.common[key] = val;
    //   });
    // }
    // return axios[params.method](params.url, params.body);
    if (params.method === "get") {
      return axios.get(params.url, { params: params.params });
    } else if (params.method === "post") {
      return axios
        .post(params.url, params.body)
        .then((res) => {
          if (res.code === 200) {
            return res.data;
          }
          return res;
        })
        .then((err) => {
          return err;
        });
    } else if (params.method === "put") {
      return axios.put(params.url, params.body);
    } else if (params.method === "delete") {
      return axios.delete(params.url, params.body);
    } else if (params.method === "patch") {
      return axios.patch(params.url, params.body);
    } else {
      return { success: false, msg: "No method provided, get, post?" };
    }
  }

  makeNewRequest(params) {
    axios.defaults.headers.common["Content-Type"] = `application/json`;
    // return axios[params.method](params.url, params.body)
    if (params.method === "get") {
      return axios.get(params.url, { params: params.params });
    } else if (params.method === "post") {
      return axios
        .post(params.url, params.body)
        .then((res) => {
          if (res.code === 200) {
            return res.data;
          }
          return res;
        })
        .then((err) => {
          return err;
        });
    } else if (params.method === "put") {
      return axios.put(params.url, params.body);
    } else if (params.method === "delete") {
      return axios.delete(params.url, params.body);
    } else if (params.method === "patch") {
      return axios.patch(params.url, params.body);
    } else {
      return { success: false, msg: "No method provided, get, post?" };
    }
  }

  makeRequestFormData(params) {
    let token = params.token || local.getItem("tokenNew");
    // axios.interceptors.request.use(setHeaders(token));
    axios.defaults.headers.common["Authorization"] = `${token}`;
    axios.defaults.headers.common["Content-Type"] = `multipart/form-data`;
    // return axios[params.method](params.url, params.body)

    if (params.method == "post") {
      return axios
        .post(params.url, params.formData)
        .then((res) => {
          if (res.code === 200) {
            return res.data;
          }
          return res;
        })
        .then((err) => {
          return err;
        });
    }

    if (params.method == "patch") {
      return axios
        .patch(params.url, params.formData)
        .then((res) => {
          if (res.code === 200) {
            return res.data;
          }
          return res;
        })
        .then((err) => {
          return err;
        });
    }
  }

  login(params) {
    return axios.post(params.url, params.body);
  }
}

