import * as types from "./CodeEdiorTypes";

export function submitEditor(payload) {
  return {
    type: types.SUBMIT_CODE,
    payload: payload,
  };
}
export function submitEditorSuccess(payload) {
  return {
    type: types.SUBMIT_CODE_SUCCESS,
    payload: payload,
  };
}
export function submitEditorError(error) {
  return {
    type: types.SUBMIT_CODE_ERROR,
    payload: error,
  };
}

export function getEditor(payload) {
  return {
    type: types.GET_CODE,
    payload: payload,
  };
}
export function getEditorSuccess(payload) {
  return {
    type: types.GET_CODE_SUCCESS,
    payload: payload,
  };
}
export function getEditorError(error) {
  return {
    type: types.GET_CODE_ERROR,
    payload: error,
  };
}
