export function* createprocess(action) {
  try {
    const result = yield call(apiJunction.makeNewRequest, {
      method: "post",
      url: `${serverAddress}/api/process/create`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.addprocessSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.addprocessError(e.response));
    // console.log("process submited error--->", e.response.data);
  }
}

