//update response
export function* updateResponse(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      url: `${serverAddress}/response/submission?responseId=${
        action.payload.responseId
      }${
        action.payload.questionId
          ? "&questionId=" + action.payload.questionId
          : ""
      }${
        action.payload.questionStatus
          ? "&questionStatus=" + action.payload.questionStatus
          : ""
      }${action.payload.type ? "&type=" + action.payload.type : ""}`,
      method: "patch",
      body: action.payload.formdata,
    });
    if (result) {
      // console.log("result", result.id, action.payload.responseId);
      if (action.payload.responseId) {
        yield put(
          ACTIONS.addEvent({
            responseId: action.payload.responseId,
            isError: false,
            event: {
              type: "api",
              level: 1,
              metadata: {
                detail: {
                  questionId: action.payload.questionId,
                  status: action.payload.questionStatus,
                  type: action.payload.type,
                },
                type: "success",
                // response: result,
              },
              timestamp: new Date().toISOString(),
            },
          })
        );
      }

      yield put(ACTIONS.updateResponseSuccess(result));
    }
  } catch (e) {
    if (action.payload.responseId) {
      yield put(
        ACTIONS.addEvent({
          responseId: action.payload.responseId,
          isError: true,
          event: {
            type: "api",
            level: 1,
            metadata: {
              detail: {
                questionId: action.payload.questionId,
                status: action.payload.questionStatus,
                type: action.payload.type,
              },
              type: "error",
              response: e.response.data,
            },
            timestamp: new Date().toISOString(),
          },
        })
      );
    }

    yield put(
      ACTIONS.updateResponseError({ singleAnswerError: e.response.data })
    );
    store.addNotification({
      title: "Error",
      message: e.response.data,
      type: "danger",
      container: "top-center",
      animationIn: ["animated", "fadeIn"],
      animationOut: ["animated", "fadeOut"],
      dismiss: {
        duration: 2000,
      },
    });
  }
}

export function* addEvent(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      url: `${serverAddress}/response/submission?responseId=${
        action.payload.responseId
      }${
        action.payload.questionId
          ? "&questionId=" + action.payload.questionId
          : ""
      }${
        action.payload.questionStatus
          ? "&questionStatus=" + action.payload.questionStatus
          : ""
      }${action.payload.type ? "&type=" + action.payload.type : ""}`,
      method: "patch",
      body: {
        event: JSON.stringify(action.payload.event),
        isError:
          action.payload.isError === true || action.payload.isError === false
            ? action.payload.isError
            : false,
        isPreviewFail:
          action.payload.isPreviewFail === true ||
          action.payload.isPreviewFail === false
            ? action.payload.isPreviewFail
            : false,
      },
    });
    // console.log('sagaggaaaaaa',result)
    if (result) {
      // console.log('resultttt',result,action.payload.event)
      yield put(ACTIONS.addEventSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.addEventError(e.response));
  }
}


function* getHealth(action = Action, req) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress2}/health`,
    });
    if (result) {
      yield put(ACTIONS.getHealthSuccess(result));
    }
  } catch (e) {
    if (e) {
      yield put(ACTIONS.getHealthError(e.response));
    
    }
  }
}
export function* UserSaga() {
  yield takeLatest(TYPES.GET_USER, getUser);
  yield takeLatest(TYPES.CREATE_USER, addUser);
  yield takeLatest(TYPES.GET_DETAILS, getUserDetails);
  yield takeLatest(TYPES.GET_SCHEDULE_DETAILS, getScheduleDetails);
  yield takeLatest(TYPES.UPDATE_DETAILS, updateuserdetailsData);
  // yield takeLatest(TYPES.CREATE_SINGLE_ANSWER, addsingleanswer);
  yield takeLatest(TYPES.CREATE_PROCESS, createprocess);
  yield takeLatest(TYPES.VERIFY_SCREENSHOT, verifyScreenshot);
  yield takeLatest(TYPES.UPDATE_RESPONSE, updateResponse);
  yield takeLatest(TYPES.ADD_EVENT, addEvent);
  yield takeLatest(TYPES.GET_HEALTH, getHealth);
}
