import React, { useState, useEffect } from "react";
// import messages from "./../../assets/Local/messages";
import { useSelector, useDispatch } from "react-redux";
import Button from "../../components/Controls/Button/Button";
import history from "../../routes/History";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as Actions from "../../store/User/UserAction";
import Profile from "../../assets/images/profile2.png";
import { logout } from "../../store/User/UserAction";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ArrowDropDownIcon from "@material-ui/icons/ArrowDropDown";
import "./Navbar.css";

function Navbar(props) {
  // const lang = useSelector(state => state.lang);
  const [anchorEl, setAnchorEl] = useState(null);
  const [name, setName] = useState("");
  // const [open, setOpen] = React.useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    if (
      this.props &&
      this.props.UserReducer &&
      this.props.UserReducer.getUserData &&
      this.props.UserReducer.getUserData.data
    ) {
      let name =
        this.props.UserReducer.getUserData.data &&
        this.props.UserReducer.getUserData.data.candidatename;

      setName(name);
    }
  }, [props.UserReducer]);
  // const message = messages[lang];
  // const switchLanguage = lang => {
  //   dispatch(setCurrentLang(lang === "ar" ? "en" : "ar"));
  // };
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    // setOpen(true);
  };

  const handleClose = (event, reason) => {
    setAnchorEl(null);
    localStorage.clear();
    history.push("/login");
    if (reason === "clickaway") {
      return;
    }

    // setOpen(false);
  };
  const handleClose1 = () => {
    setAnchorEl(null);
  };

  // const submitClick = () => {
  //   dispatch(logout());
  //   localStorage.clear();
  //   history.push("/login");
  // };

  // const getTitle = () => {
  //   const title = superAdminLinks.map
  // }

  return (
    <>
      <nav
        // onClick={handleClick}
        className='navbar navbar-dark bg-white'
      >
        {window.location.pathname === "/dashboard" ? (
          <a className='navbar-brand title' id='title'>
            Dashboard
          </a>
        ) : window.location.pathname === "/jd" ? (
          <a className='navbar-brand title' id='title'>
            Job Description List
          </a>
        ) : window.location.pathname === "/addjobtitle" ? (
          <a className='navbar-brand title' id='title'>
            Add Jd Title
          </a>
        ) : window.location.pathname === "/AddJD" ? (
          <a className='navbar-brand title' id='title'>
            Add Job Description
          </a>
        ) : window.location.pathname === "/jddata" ? (
          <a className='navbar-brand title' id='title'>
            Job Description Data
          </a>
        ) : window.location.pathname === "/Schedule" ? (
          <a className='navbar-brand title' id='title'>
            Add Schedule
          </a>
        ) : window.location.pathname === "/resumeSourcing" ? (
          <a className='navbar-brand title' id='title'>
            Add Resume Sourcing
          </a>
        ) : window.location.pathname === "/followUp" ? (
          <a className='navbar-brand title' id='title'>
            Follow Up List
          </a>
        ) : window.location.pathname === "/response" ? (
          <a className='navbar-brand title' id='title'>
            Response
          </a>
        ) : window.location.pathname === "/questions" ? (
          <a className='navbar-brand title' id='title'>
            Add Questions
          </a>
        ) : window.location.pathname === "/selected" ? (
          <a className='navbar-brand title' id='title'>
            Selected
          </a>
        ) : window.location.pathname === "/shortlist" ? (
          <a className='navbar-brand title' id='title'>
            Shortlist
          </a>
        ) : window.location.pathname === "/timeline" ? (
          <a className='navbar-brand title' id='title'>
            Timeline
          </a>
        ) : window.location.pathname === "/user-management" ? (
          <a className='navbar-brand title' id='title'>
            User Management
          </a>
        ) : null}
        <div className='d-flex align-items-center'>
          <div className='navbar-profile'>
            <img src={Profile} className='image-profile' />
            <span className='navbar-profile-name'>{name}</span>
            <span
              style={{ cursor: "pointer" }}
              aria-controls='simple-menu'
              aria-haspopup='true'
              onClick={handleClick}
            >
              <ArrowDropDownIcon />
            </span>
          </div>
          <Menu
            id='simple-menu'
            className='menuItem-position'
            anchorEl={anchorEl}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={handleClose1}
          >
            <MenuItem onClick={handleClose}>Logout</MenuItem>
          </Menu>
          {/* <div>
            <Button
              width="100px"
              height="39px"
              borderRadius="6px"
              margin="4px"
              onClick={() => submitClick()}
            >
              SignOut
            </Button>
          </div> */}
        </div>
      </nav>
    </>
  );
}

const mapStateToProps = (state) => ({
  UserReducer: state.UserReducer,
});
const mapDispatchToProps = (dispatch) => {
  return {
    UserAction: bindActionCreators(Actions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
