import { call, put } from "redux-saga/effects";
import * as ACTIONS from "./CodeEdiorAction";
import { takeLatest } from "redux-saga/effects";
import * as TYPES from "./CodeEdiorTypes";
import apiJunction from "../../utils/api";
import {serverAddress} from "../../config";
// import { store } from "react-notifications-component";/

export function* submitEditor(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "post",
      url: `${serverAddress}/execution`,
      body: action.payload,
    });
    if (result) {
      yield put(ACTIONS.submitEditorSuccess(result));
      // store.addNotification({
      //   title: "Success",
      //   message: "Code submitted successfully",
      //   type: "success",
      //   container: "top-center",
      //   animationIn: ["animated", "fadeIn"],
      //   animationOut: ["animated", "fadeOut"],
      //   dismiss: {
      //     duration: 2000,
      //   },
      // });
    }
  } catch (e) {
    yield put(ACTIONS.submitEditorError(e.response));
  }
}

export function* getEditor(action) {
  try {
    const result = yield call(apiJunction.makeRequest, {
      method: "get",
      url: `${serverAddress}/execution/${action.payload.id}`,
    });
    if (result) {
      yield put(ACTIONS.getEditorSuccess(result));
    }
  } catch (e) {
    yield put(ACTIONS.getEditorError(e.response));
  }
}

export function* CodeEditorSaga() {
  yield takeLatest(TYPES.SUBMIT_CODE, submitEditor);
  yield takeLatest(TYPES.GET_CODE, getEditor);
}
