import React from "react";
import { NavLink, Link } from "react-router-dom";
import MenuButton from "../assets/images/menu-button.svg";
import "./sideNav.scss";
import responsedropdown from "../assets/images/responseDropdownIcon.svg";
import ArrowDropUpIcon from "@material-ui/icons/ArrowDropUp";

const superAdminLinks = [
  {
    main: "Dashboard",
    subLinks: [
      {
        link: "/dashboard",
        heading: "Dashboard",
        exact: true,
        class: "dashboard",
      },
    ],
  },
  {
    main: "JD List",
    subLinks: [
      {
        link: "/jd",
        heading: "JD List",
        exact: true,
        class: "jdList",
      },
    ],
  },
  {
    main: "Schedule",
    subLinks: [
      {
        link: "/Schedule",
        heading: "Schedule",
        exact: true,
        class: "schedule",
      },
    ],
  },
  {
    main: "Resume Sourcing",
    subLinks: [
      {
        link: "/resumeSourcing",
        heading: "Resume Sourcing",
        exact: true,
        class: "resumeSourcing",
      },
    ],
  },
  {
    main: "Follow Up",
    subLinks: [
      {
        link: "/followUp",
        heading: "Follow Up",
        exact: true,
        class: "followup",
      },
    ],
  },
  {
    main: "Response",
    subLinks: [
      {
        link: "/response",
        heading: "Response",
        exact: true,
        class: "response",
      },
    ],
  },
  {
    main: "Shortlist",
    subLinks: [
      {
        link: "/shortlist",
        heading: "Shortlist",
        exact: true,
        class: "toggle-off",
      },
    ],
  },
  {
    main: "Selected",
    subLinks: [
      {
        link: "/selected",
        heading: "Selected",
        exact: true,
        class: "toggle-off",
      },
    ],
  },
  {
    main: "Timeline",
    subLinks: [
      {
        link: "/timeline",
        heading: "Timeline",
        exact: true,
        class: "toggle-off",
      },
    ],
  },
  {
    main: "User Management",
    subLinks: [
      {
        link: "/user-management",
        heading: "User Management",
        exact: true,
        class: "userManagement",
      },
    ],
  },
];

class SideNav extends React.Component {
  state = {
    subLinksTrue: false,
    showDropdown: false,
  };

  handleResponseMenu = () => {
    const { showDropdown } = this.state;
    this.setState({
      showDropdown: !showDropdown,
    });
  };

  render() {
    let sidebarValues = superAdminLinks;
    const { showDropdown } = this.state;
    return (
      <nav id={this.props.navClass ? "sidebar" : "sidebar-toggle"}>
        <>
          <img
            className="hamstyle"
            style={{ float: "left", marginLeft: "30px" }}
            src={MenuButton}
            alt="menu-button"
            onClick={() => this.props.toggleClass()}
          />

          <ul className="list-unstyled">
            {sidebarValues.map((row, index) => {
              return (
                <React.Fragment key={("mainLink-", row.main)}>
                  {row.subLinks.map((sublink, subIndex) => {
                    if (sublink.heading === "Response") {
                      return (
                        <div key={subIndex}>
                          <li key={("sublink-", sublink.heading)}>
                            <div
                              id="topBorder"
                              style={{
                                borderTopWidth: "thin",
                                borderTopColor: "#8A8A8A",
                                opacity: "25%",
                              }}
                            >
                              {" "}
                            </div>
                            <div
                              className={
                                this.props.navClass
                                  ? "small-response-div"
                                  : "large-response-div"
                              }
                            >
                              <Link
                                to={sublink.link}
                                title={sublink.heading}
                                className={sublink.class}
                                activeClassName={
                                  !this.props.navClass
                                    ? "large-user-selected"
                                    : "small-user-selected"
                                }
                              >
                                <i>{sublink.icon}</i>
                                {!this.props.navClass ? (
                                  <span>{sublink.heading} </span>
                                ) : null}
                              </Link>
                              {!this.props.navClass ? (
                                <>
                                  <span
                                    className="dropdown-icon"
                                    onClick={() => this.handleResponseMenu()}
                                  >
                                    {showDropdown ? (
                                      <ArrowDropUpIcon
                                        style={{
                                          width: "20px",
                                          height: "20px",
                                          color: "#fff",
                                        }}
                                        className="response-dropdown"
                                      />
                                    ) : (
                                      <img
                                        src={responsedropdown}
                                        className="response-dropdown"
                                        alt="responsedrop"
                                      />
                                    )}
                                  </span>
                                </>
                              ) : null}
                            </div>
                          </li>
                        </div>
                      );
                    } else if (
                      sublink.heading == "Shortlist" ||
                      sublink.heading == "Selected" ||
                      sublink.heading == "Timeline"
                    ) {
                      if (showDropdown) {
                        return (
                          <div key={subIndex} style={{ paddingBottom: "4px" }}>
                            <li key={("sublink-", sublink.heading)}>
                              <div
                                id="topBorder"
                                style={{
                                  borderTopWidth: "thin",
                                  borderTopColor: "#8A8A8A",
                                  opacity: "25%",
                                }}
                              >
                                {" "}
                              </div>
                              <NavLink
                                to={sublink.link}
                                title={sublink.heading}
                                className={sublink.class}
                                // activeClassName="user-selected"
                                activeClassName={
                                  !this.props.navClass
                                    ? "large-user-selected"
                                    : "small-user-selected"
                                }
                              >
                                <i>{sublink.icon}</i>
                                {!this.props.navClass ? (
                                  <span>{sublink.heading}</span>
                                ) : null}
                              </NavLink>
                            </li>
                          </div>
                        );
                      } else {
                      }
                    } else {
                      return (
                        <div key={subIndex} style={{ paddingBottom: "4px" }}>
                          <li key={("sublink-", sublink.heading)}>
                            <div
                              id="topBorder"
                              style={{
                                borderTopWidth: "thin",
                                borderTopColor: "#8A8A8A",
                                opacity: "25%",
                              }}
                            >
                              {" "}
                            </div>
                            <NavLink
                              to={sublink.link}
                              title={sublink.heading}
                              className={sublink.class}
                              // activeClassName="user-selected"
                              activeClassName={
                                !this.props.navClass
                                  ? "large-user-selected"
                                  : "small-user-selected"
                              }
                            >
                              <i>{sublink.icon}</i>
                              {!this.props.navClass ? (
                                <span>{sublink.heading}</span>
                              ) : null}
                            </NavLink>
                          </li>
                        </div>
                      );
                    }
                  })}
                </React.Fragment>
              );
            })}
          </ul>
        </>
      </nav>
    );
  }
}
export default SideNav;
