import * as types from "./UserTypes";
const INITIAL_STATE = {
  CurrentUser: [],
  getDetailsData: [],
  isLoggedIn: false,
  isGetUserData: false,
  isGetDetailsData: false,
  getUserData: [],
  isUserDetailsUpdated: false,
  isUserDataAdded: false,
  isUserquestionDataAdded: false,
  isDeletedUserData: false,
  isUserDataUpdated: false,
  isprocessDataAdded: false,
  screenShot: [],
  isScreenShotDataArrived: false,
  singleAnswerError: "",
  getScheduleDetails: [],
  scheduleDetailsArrived: false,
  scheduleDetailsError: false,
  singleAnswerData: [],
  userError: "",
  singleAnswerProgress: 0,
  addUserProgress: 0,
  userDataResponse: [],
  isResponseUpdated: false,
  updateError: "",
  updateResponseData: [],
  isAddEvent: false,
  addEvent: [],
  capturingNew:false,
  abortNew:false,
  accessNew:false,
  screenRecordedFile:[],
  streamNew:false,
  health:[],
  isGetHealth:false,
  isHealthError:false

};

export function UserReducer(state = INITIAL_STATE, action) {
  // console.log('actionpayload---->',action)
  switch (action.type) {
    case types.GET_USER:
      return {
        ...state,
        isGetResponse: false,
      };

    case types.GET_USER_SUCCESS:
      return {
        ...state,
        getUserData: action.payload,
        isGetUserData: true,
      };

    case types.GET_USER_ERROR:
      return {
        ...state,
        isGetUserData: false,
      };

    case types.CREATE_USER:
      return {
        ...state,
        isUserDataAdded: false,
        userDataResponse: [],
      };

    case types.CREATE_USER_SUCCESS:
      return {
        ...state,
        isUserDataAdded: true,
        userDataResponse: action.payload,
      };
    case types.CREATE_USER_ERROR:
      return {
        ...state,
        isUserDataAdded: false,
        userError: action.payload.error,
        userDataResponse: [],
      };
    case types.CREATE_USER_PROGRESS:
      return {
        ...state,
        addUserProgress: action.payload.progress,
      };

    case types.GET_DETAILS:
      return {
        ...state,
        isGetDetailsData: false,
      };

    case types.GET_DETAILS_SUCCESS:
      return {
        ...state,
        getDetailsData: action.payload,
        isGetDetailsData: true,
      };

    case types.GET_DETAILS_ERROR:
      return {
        ...state,
        isGetDetailsData: false,
      };
    case types.UPDATE_DETAILS:
      return {
        ...state,
        isUserDetailsUpdated: false,
      };

    case types.UPDATE_DETAILS_SUCCESS:
      return {
        ...state,
        isUserDetailsUpdated: true,
      };

    case types.UPDATE_DETAILS_ERROR:
      return {
        ...state,
        isUserDetailsUpdated: false,
      };
    case types.CREATE_SINGLE_ANSWER:
      return {
        ...state,
        isUserquestionDataAdded: false,
        singleAnswerError: "",
        singleAnswerData: [],
      };

    case types.CREATE_SINGLE_ANSWER_PROGRESS:
      return {
        ...state,
        singleAnswerProgress: action.payload.progress,
      };

    case types.CREATE_SINGLE_ANSWER_SUCCESS:
      return {
        ...state,
        isUserquestionDataAdded: true,
        singleAnswerError: "",
        singleAnswerData: action.payload,
      };

    case types.CREATE_SINGLE_ANSWER_ERROR:
      return {
        ...state,
        isUserquestionDataAdded: false,
        singleAnswerError: action.payload.singleAnswerError,
        singleAnswerData: [],
      };
    case types.CREATE_PROCESS:
      return {
        ...state,
        isprocessDataAdded: false,
      };

    case types.CREATE_PROCESS_SUCCESS:
      return {
        ...state,
        isprocessDataAdded: true,
      };

    case types.CREATE_PROCESS_ERROR:
      return {
        ...state,
        isprocessDataAdded: false,
      };

    case types.VERIFY_SCREENSHOT:
      return {
        ...state,
        isScreenShotDataArrived: false,
        screenShot: [],
      };

    case types.VERIFY_SCREENSHOT_SUCCESS:
      return {
        ...state,
        isScreenShotDataArrived: true,
        screenShot: action.payload,
      };

    case types.VERIFY_SCREENSHOT_ERROR:
      return {
        ...state,
        isScreenShotDataArrived: false,
        screenshotError: action.payload.screenshotError,
      };
    case types.GET_SCHEDULE_DETAILS:
      return {
        ...state,
        scheduleDetailsArrived: false,
      };

    case types.GET_SCHEDULE_DETAILS_SUCCESS:
      return {
        ...state,
        getScheduleDetails: action.payload,
        scheduleDetailsArrived: true,
      };

    case types.GET_SCHEDULE_DETAILS_ERROR:
      return {
        ...state,
        scheduleDetailsArrived: false,
        scheduleDetailsError: action.payload.scheduleError,
      };
    case types.UPDATE_RESPONSE:
      return {
        ...state,
        isResponseUpdated: false,
        updateError: "",
        updateResponseData: [],
      };

    case types.UPDATE_RESPONSE_SUCCESS:
      return {
        ...state,
        isResponseUpdated: true,
        updateError: "",
        updateResponseData: action.payload,
      };

    case types.UPDATE_RESPONSE_ERROR:
      return {
        ...state,
        isResponseUpdated: false,
        updateError: action.payload.updateError,
        updateResponseData: [],
      };
    case types.ADD_EVENT:
      return {
        ...state,
        addEvent: [],
        isAddEvent: false,
      };

    case types.ADD_EVENT_SUCCESS:
      return {
        ...state,
        addEvent: action.payload,
        isAddEvent: true,
      };

    case types.ADD_EVENT_ERROR:
      return {
        ...state,
        addEvent: action.payload,
        isAddEvent: false,
      };
      case types.GET_ACCESS:
        return {
          ...state,
          accessNew:true
        };
        case types.START_CAPTURE:
          return {
            ...state,
            capturingNew:true
          };
          case types.STOP_CAPTURE:
            return {
              ...state,
              capturingNew:false
            };
            case types.ABORT_CAPTURE:
              return {
                ...state,
                abortNew:action.payload
              };
              case types.CLOSE_ACCESS:
                return {
                  ...state,
                 accessNew:false
                };
                case types.ADD_SCREEN_RECORDING:
                  return {
                    ...state,
                   screenRecordedFile:action.payload
                  };
                  case types.ADD_STREAM:
                    return {
                      ...state,
                     streamNew:action.payload
                    };
                    case types.GET_HEALTH:
                      return {
                        ...state,
                        isGetHealth: false,
                        isHealthError:false
                      };
                
                    case types.GET_HEALTH_SUCCESS:
                      return {
                        ...state,
                        isGetHealth: true,
                        isHealthError:false,
                        health: action.payload
                      };
                
                    case types.GET_HEALTH_ERROR:
                      return {
                        ...state,
                        isGetHealth: false,
                        isHealthError:true
                      };
                


    default:
      return state;
  }
}
